.partnership {
    position: relative;
    height: 700px;
    font-weight: 500;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
.partnership::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url('./../images/herobackgroundImg.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    filter: brightness(10%);
    z-index: -1;
}
.partnership__title{
    color: #fff;
    margin-bottom: 100px;
    font-size: 32px;
    font-weight: 600;
    line-height: 30px;
}
.partnership__container{
    display: flex;
    gap: 120px;
}
.partnership__container_main{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
}
.partnership__container_text_container{
    display: flex;
    align-items: flex-start;
    gap: 10px;
}
.partnership__container_text{
    max-width: 537px;
    color: #fff;
    font-size: 18px;
    line-height: 28px;
    font-weight: 400;
    margin-top: -5px;
    font-family: "Montserrat Alternates", sans-serif;
}
.zoom-image{
    object-fit: cover; /* Ensures the image fills the container proportionally */
    transition: transform 0.5s ease; 
}

.zoom-image:hover {
    transform: scale(1.2); /* Scale up the image slightly */
  }